// Utility funkce pro validaci email a telefon

/**
 * Pokroil validace emailov adresy
 * @param {string} email - Email k validaci
 * @returns {Promise<boolean|string>} - true pokud je email validn, jinak chybov zprva
 */
export const validateEmail = async (email) => {
  if (!email || String(email).trim() === '') return true;

  const trimmedEmail = String(email).trim();

  // Zkladn regex pro formt emailu (RFC 5322 compliant)
  const emailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/;
  
  if (!emailRegex.test(trimmedEmail)) {
    return 'Neplatn formt e-mailov adresy';
  }

  // Kontrola dlky
  if (trimmedEmail.length > 254) {
    return 'E-mailov adresa je pli dlouh';
  }

  const parts = trimmedEmail.split('@');
  if (parts.length !== 2) {
    return 'Neplatn formt e-mailov adresy';
  }

  const [localPart, domain] = parts;
  
  // Kontrola lokln sti
  if (localPart.length === 0 || localPart.length > 64) {
    return 'Neplatn lokln st e-mailov adresy';
  }

  // Kontrola domny
  if (domain.length === 0 || domain.length > 253) {
    return 'Neplatn domna e-mailov adresy';
  }

  // Kontrola zakzanch znak v domn
  if (domain.startsWith('-') || domain.endsWith('-') || domain.includes('..') || domain.startsWith('.') || domain.endsWith('.')) {
    return 'Neplatn domna e-mailov adresy';
  }

  // Kontrola TLD (mus mt alespo jednu teku a domnu)
  if (!domain.includes('.') || domain.split('.').length < 2) {
    return 'E-mailov adresa mus obsahovat platnou domnu';
  }

  // Kontrola nkterch bnch chyb
  const commonDomains = ['gmail.com', 'seznam.cz', 'centrum.cz', 'email.cz', 'outlook.com', 'hotmail.com', 'yahoo.com'];
  const domainLower = domain.toLowerCase();
  
  // Kontrola astch peklep
  const typos = {
    'gmail.co': 'gmail.com',
    'gmail.cz': 'gmail.com',
    'gmai.com': 'gmail.com',
    'gmial.com': 'gmail.com',
    'seznam.c': 'seznam.cz',
    'seznam.com': 'seznam.cz',
    'centrm.cz': 'centrum.cz',
    'centrum.c': 'centrum.cz'
  };

  if (typos[domainLower]) {
    return `Mon jste mysleli: ${localPart}@${typos[domainLower]}`;
  }

  try {
    // Pokus o oven existence domny pomoc DNS
    const response = await fetch(`https://dns.google/resolve?name=${domain}&type=MX&cd=false`, {
      method: 'GET',
      headers: {
        'Accept': 'application/json',
      },
      signal: AbortSignal.timeout(5000) // 5 sekund timeout
    });

    if (!response.ok) {
      console.warn('DNS oven selhalo, pokrauji bez oven domny');
      return true; // Pokud DNS sluba nen dostupn, povolme email
    }

    const data = await response.json();
    
    // Kontrola existence MX zznam
    if (data.Status === 0 && data.Answer && data.Answer.length > 0) {
      // Nali jsme MX zznamy, domna existuje
      return true;
    } else {
      // Zkusme jet A zznam (nkter domny nemaj MX ale maj A)
      try {
        const aResponse = await fetch(`https://dns.google/resolve?name=${domain}&type=A&cd=false`, {
          signal: AbortSignal.timeout(3000)
        });
        if (aResponse.ok) {
          const aData = await aResponse.json();
          if (aData.Status === 0 && aData.Answer && aData.Answer.length > 0) {
            return true;
          }
        }
      } catch (aError) {
        console.warn('A zznam kontrola selhala:', aError);
      }
      
      return 'E-mailov domna neexistuje nebo nem potovn server';
    }
  } catch (error) {
    console.warn('Chyba pi ovovn e-mailov domny:', error);
    return true; // Pokud oven sele, povolme email
  }
};

/**
 * Formtovn telefonnho sla
 * @param {string} phone - Telefonn slo k formtovn
 * @returns {string} - Formtovan telefonn slo
 */
export const formatPhoneNumber = (phone) => {
  if (!phone) return '';

  // Konverze na string a odstrann vech ne-selnch znak krom +
  let cleaned = String(phone).replace(/[^\d+]/g, '');

  // Pokud slo nezana +, pidme +420
  if (!cleaned.startsWith('+')) {
    // Pokud zan 00, nahradme +
    if (cleaned.startsWith('00')) {
      cleaned = '+' + cleaned.substring(2);
    } else {
      cleaned = '+420' + cleaned;
    }
  }

  // Formtovn pro esk slo (+420)
  if (cleaned.startsWith('+420')) {
    const number = cleaned.substring(4);
    if (number.length === 9) {
      return `+420 ${number.substring(0, 3)} ${number.substring(3, 6)} ${number.substring(6)}`;
    }
  }

  // Formtovn pro slovensk slo (+421)
  if (cleaned.startsWith('+421')) {
    const number = cleaned.substring(4);
    if (number.length === 9) {
      return `+421 ${number.substring(0, 3)} ${number.substring(3, 6)} ${number.substring(6)}`;
    }
  }

  // Pro ostatn zem vrtme pvodn formt
  return cleaned;
};

/**
 * Validace telefonnho sla
 * @param {string} phone - Telefonn slo k validaci
 * @returns {boolean|string} - true pokud je slo validn, jinak chybov zprva
 */
export const validatePhoneNumber = (phone) => {
  if (!phone) return true;

  // Konverze na string a odstrann vech ne-selnch znak krom +
  let cleaned = String(phone).replace(/[^\d+]/g, '');

  // Pokud slo nezana +, pidme +420
  if (!cleaned.startsWith('+')) {
    if (cleaned.startsWith('00')) {
      cleaned = '+' + cleaned.substring(2);
    } else {
      cleaned = '+420' + cleaned;
    }
  }

  // Validace eskho sla (+420)
  if (cleaned.startsWith('+420')) {
    const number = cleaned.substring(4);
    if (number.length !== 9) {
      return 'esk telefonn slo mus mt 9 slic po pedvolb +420';
    }
    // esk mobiln sla zanaj 6, 7 nebo 9
    const firstDigit = number[0];
    if (!['6', '7', '9'].includes(firstDigit)) {
      return 'Neplatn esk telefonn slo (mus zanat 6, 7 nebo 9)';
    }
    return true;
  }

  // Validace slovenskho sla (+421)
  if (cleaned.startsWith('+421')) {
    const number = cleaned.substring(4);
    if (number.length !== 9) {
      return 'Slovensk telefonn slo mus mt 9 slic po pedvolb +421';
    }
    // Slovensk mobiln sla zanaj 9
    const firstDigit = number[0];
    if (firstDigit !== '9') {
      return 'Neplatn slovensk mobiln slo (mus zanat 9)';
    }
    return true;
  }

  return 'Podporujeme pouze esk (+420) a slovensk (+421) telefonn sla';
};

/**
 * Hook pro automatick formtovn telefonnho sla pi psan
 * @param {Function} setValue - React Hook Form setValue funkce
 * @param {string} fieldName - Nzev pole ve formuli
 * @returns {Function} - onChange handler
 */
export const usePhoneFormatter = (setValue, fieldName) => {
  return (e) => {
    const formatted = formatPhoneNumber(String(e.target.value));
    setValue(fieldName, formatted);
  };
};
