import { useFormContext, useWatch } from 'react-hook-form'
import { DollarSign, Zap, Share2, Flame, FileImage, Factory } from 'lucide-react'
import { useEffect } from 'react'
import FileUploadField from '../FileUploadField'

const FormStep8 = ({ formId }) => {
  const { register, control, setValue, watch } = useFormContext()
  
  // Watch for billing method to show conditional fields
  const billingMethod = useWatch({
    control,
    name: 'billingMethod'
  })

  // Watch fix and spot percentages for automatic calculation
  const fixPercentage = useWatch({
    control,
    name: 'fixPercentage'
  })
  
  const spotPercentage = useWatch({
    control,
    name: 'spotPercentage'
  })

  // Auto-calculate complementary percentage
  useEffect(() => {
    if (billingMethod === 'gradual') {
      const fixValue = parseFloat(fixPercentage) || 0
      const spotValue = parseFloat(spotPercentage) || 0
      
      // If fix percentage changed, update spot percentage
      if (fixValue > 0 && fixValue <= 100) {
        const newSpotValue = 100 - fixValue
        if (newSpotValue !== spotValue) {
          setValue('spotPercentage', newSpotValue.toString())
        }
      }
      // If spot percentage changed, update fix percentage  
      else if (spotValue > 0 && spotValue <= 100) {
        const newFixValue = 100 - spotValue
        if (newFixValue !== fixValue) {
          setValue('fixPercentage', newFixValue.toString())
        }
      }
    }
  }, [fixPercentage, spotPercentage, billingMethod, setValue])

  return (
    <div className="space-y-6">
      <div className="text-center mb-8">
        <div className="flex items-center justify-center mb-4">
          <DollarSign className="h-8 w-8 text-primary-600 mr-3" />
          <h2 className="text-2xl font-bold text-gray-900">
            8. Energetick dotaznk
          </h2>
        </div>
        <p className="text-gray-600">
          Doplujc informace o cench energi a dalch spotebch
        </p>
      </div>

      <div className="space-y-6">
        {/* Zpsob tovn energi */}
        <div className="bg-blue-50 p-4 rounded-lg border border-blue-200">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">
            Zpsob tovn energi
          </h3>
          
          <div className="space-y-3">
            <label className="flex items-center">
              <input 
                type="radio" 
                className="form-radio mr-3"
                value="spot"
                {...register('billingMethod')}
              />
              <span className="font-medium">SPOT</span> - cena se mn podle spotovho trhu
            </label>
            <label className="flex items-center">
              <input 
                type="radio" 
                className="form-radio mr-3"
                value="fix"
                {...register('billingMethod')}
              />
              <span className="font-medium">FIX</span> - fixn cena po dobu smlouvy
            </label>
            <label className="flex items-center">
              <input 
                type="radio" 
                className="form-radio mr-3"
                value="gradual"
                {...register('billingMethod')}
              />
              <span className="font-medium">Postupn nkup</span> - kombinace vce zpsob
            </label>
          </div>

          {/* Podmnn pole pro SPOT */}
          {billingMethod === 'spot' && (
            <div className="mt-4 p-4 bg-blue-100 rounded-lg border border-blue-300">
              <h4 className="font-medium text-blue-900 mb-3">Nastaven SPOT tarifu</h4>
              <div>
                <label className="form-label">Pirka ke spotov cen:</label>
                <div className="flex items-center gap-2">
                  <input
                    type="number"
                    step="0.01"
                    className="form-input w-32"
                    placeholder="0.50"
                    {...register('spotSurcharge')}
                  />
                  <span className="text-gray-600">K/kWh</span>
                </div>
                <p className="text-xs text-blue-700 mt-1">
                  Pirka k aktuln spotov cen elektiny
                </p>
              </div>
            </div>
          )}

          {/* Podmnn pole pro FIX */}
          {billingMethod === 'fix' && (
            <div className="mt-4 p-4 bg-green-100 rounded-lg border border-green-300">
              <h4 className="font-medium text-green-900 mb-3">Nastaven FIX tarifu</h4>
              <div>
                <label className="form-label">Aktuln cena silov elektiny:</label>
                <div className="flex items-center gap-2">
                  <input
                    type="number"
                    step="0.01"
                    className="form-input w-32"
                    placeholder="2.50"
                    {...register('fixPrice')}
                  />
                  <span className="text-gray-600">K/kWh</span>
                </div>
                <p className="text-xs text-green-700 mt-1">
                  Cena bez distribunch poplatk a slueb
                </p>
              </div>
            </div>
          )}

          {/* Podmnn pole pro postupn nkup */}
          {billingMethod === 'gradual' && (
            <div className="mt-4 p-4 bg-purple-100 rounded-lg border border-purple-300">
              <h4 className="font-medium text-purple-900 mb-4">Nastaven postupnho nkupu</h4>
              
              <div className="space-y-4">
                {/* Pomr FIX/SPOT */}
                <div>
                  <label className="form-label">Pomr FIX/SPOT:</label>
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="text-sm text-gray-600">FIX podl:</label>
                      <div className="flex items-center gap-2">
                        <input
                          type="number"
                          min="0"
                          max="100"
                          className="form-input w-20"
                          placeholder="60"
                          {...register('fixPercentage')}
                          onChange={(e) => {
                            const value = parseFloat(e.target.value) || 0
                            if (value >= 0 && value <= 100) {
                              setValue('fixPercentage', e.target.value)
                              setValue('spotPercentage', (100 - value).toString())
                            }
                          }}
                        />
                        <span className="text-gray-600">%</span>
                      </div>
                    </div>
                    <div>
                      <label className="text-sm text-gray-600">SPOT podl:</label>
                      <div className="flex items-center gap-2">
                        <input
                          type="number"
                          min="0"
                          max="100"
                          className="form-input w-20"
                          placeholder="40"
                          {...register('spotPercentage')}
                          onChange={(e) => {
                            const value = parseFloat(e.target.value) || 0
                            if (value >= 0 && value <= 100) {
                              setValue('spotPercentage', e.target.value)
                              setValue('fixPercentage', (100 - value).toString())
                            }
                          }}
                        />
                        <span className="text-gray-600">%</span>
                      </div>
                    </div>
                  </div>
                  <p className="text-xs text-purple-700 mt-2">
                    Souet mus bt 100%. Pi zmn jedn hodnoty se druh automaticky dopot.
                  </p>
                </div>

                {/* FIX cena */}
                <div>
                  <label className="form-label">FIX cena silov elektiny:</label>
                  <div className="flex items-center gap-2">
                    <input
                      type="number"
                      step="0.01"
                      className="form-input w-32"
                      placeholder="2.50"
                      {...register('gradualFixPrice')}
                    />
                    <span className="text-gray-600">K/kWh</span>
                  </div>
                </div>

                {/* SPOT pirka */}
                <div>
                  <label className="form-label">SPOT pirka:</label>
                  <div className="flex items-center gap-2">
                    <input
                      type="number"
                      step="0.01"
                      className="form-input w-32"
                      placeholder="0.50"
                      {...register('gradualSpotSurcharge')}
                    />
                    <span className="text-gray-600">K/kWh</span>
                  </div>
                </div>

                <p className="text-xs text-purple-700">
                  Kombinace fixn a spotov ceny podle zadanho pomru
                </p>
              </div>
            </div>
          )}

          {/* Nahrn vytovn */}
          <div className="mt-4">
            <FileUploadField
              name="billingDocuments"
              label="Nahrt vytovn (doloit zpsob tovn):"
              accept=".pdf,.jpg,.jpeg,.png,.doc,.docx"
              multiple={true}
              formId={formId}
              register={register}
              setValue={setValue}
              watch={watch}
              helpText="Podporovan formty: PDF, obrzky (JPG, PNG), Word dokumenty"
            />
          </div>
        </div>

        {/* Aktuln cena energi */}
        <div className="bg-green-50 p-4 rounded-lg border border-green-200">
          <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <DollarSign className="h-5 w-5 mr-2" />
            Aktuln cena energi
          </h3>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="form-label">Cena za kWh (bez DPH):</label>
              <div className="flex">
                <input
                  type="number"
                  step="0.01"
                  className="form-input rounded-r-none"
                  placeholder="Cena"
                  {...register('currentEnergyPrice')}
                />
                <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                  K/kWh
                </span>
              </div>
            </div>
            <div>
              <label className="form-label">Jak je pro Vs dleit zelen energie (1-10):</label>
              <input
                type="range"
                min="1"
                max="10"
                className="w-full h-2 bg-gray-200 rounded-lg appearance-none cursor-pointer"
                {...register('priceImportance')}
              />
              <div className="flex justify-between text-xs text-gray-500 mt-1">
                <span>1 (nejmn dleit)</span>
                <span>10 (velmi dleit)</span>
              </div>
            </div>
          </div>
        </div>


        {/* Sdlen elektiny */}
        <div className="bg-purple-50 p-4 rounded-lg border border-purple-200">
          <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <Share2 className="h-5 w-5 mr-2" />
            Sdlen elektiny
          </h3>
          
          <div>
            <label className="form-label">Vyuvte sdlen elektiny?</label>
            <div className="flex gap-6 mt-3">
              <label className="flex items-center">
                <input 
                  type="radio" 
                  className="form-radio mr-2"
                  value="yes"
                  {...register('electricitySharing')}
                />
                Ano
              </label>
              <label className="flex items-center">
                <input 
                  type="radio" 
                  className="form-radio mr-2"
                  value="no"
                  {...register('electricitySharing')}
                />
                Ne
              </label>
            </div>
            
            {useWatch({ control, name: 'electricitySharing' }) === 'yes' && (
              <div className="mt-4">
                <label className="form-label">Detaily sdlen:</label>
                <textarea
                  className="form-input mt-2"
                  rows={3}
                  placeholder="Popite jak vyuvte sdlen elektiny, s km sdlte, kolik kWh..."
                  {...register('sharingDetails')}
                />
              </div>
            )}
          </div>
        </div>

        {/* Spoteba plynu */}
        <div className="bg-orange-50 p-4 rounded-lg border border-orange-200">
          <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <Flame className="h-5 w-5 mr-2" />
            Spoteba plynu
          </h3>
          
          <div>
            <label className="form-label">Mte pipojen plyn?</label>
            <div className="flex gap-6 mt-3">
              <label className="flex items-center">
                <input 
                  type="radio" 
                  className="form-radio mr-2"
                  value="yes"
                  {...register('hasGas')}
                />
                Ano
              </label>
              <label className="flex items-center">
                <input 
                  type="radio" 
                  className="form-radio mr-2"
                  value="no"
                  {...register('hasGas')}
                />
                Ne
              </label>
            </div>
            
            {useWatch({ control, name: 'hasGas' }) === 'yes' && (
              <div className="mt-4 space-y-4">
                <div>
                  <label className="form-label">Ron spoteba plynu:</label>
                  <div className="flex">
                    <input
                      type="number"
                      className="form-input rounded-r-none"
                      placeholder="Spoteba"
                      {...register('gasConsumption')}
                    />
                    <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                      m/rok
                    </span>
                  </div>
                </div>
                
                <div>
                  <label className="form-label">Ron nklady na plyn:</label>
                  <div className="flex">
                    <input
                      type="number"
                      className="form-input rounded-r-none"
                      placeholder="Nklady"
                      {...register('gasBill')}
                    />
                    <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                      K/rok
                    </span>
                  </div>
                </div>

                <div>
                  <label className="form-label">Vyuit plynu:</label>
                  <div className="space-y-2 mt-2">
                    <label className="flex items-center">
                      <input 
                        type="checkbox" 
                        className="form-checkbox mr-2"
                        {...register('gasUsage.heating')}
                      />
                      Vytpn
                    </label>
                    <label className="flex items-center">
                      <input 
                        type="checkbox" 
                        className="form-checkbox mr-2"
                        {...register('gasUsage.hotWater')}
                      />
                      Ohev vody
                    </label>
                    <label className="flex items-center">
                      <input 
                        type="checkbox" 
                        className="form-checkbox mr-2"
                        {...register('gasUsage.technology')}
                      />
                      Technologie/vroba
                    </label>
                    <label className="flex items-center">
                      <input 
                        type="checkbox" 
                        className="form-checkbox mr-2"
                        {...register('gasUsage.cooking')}
                      />
                      Vaen
                    </label>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Dal spoteby mimo energetickou bilanci */}
        <div className="bg-yellow-50 p-4 rounded-lg border border-yellow-200">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">
            Dal spoteby mimo energetickou bilanci
          </h3>
          
          <div className="space-y-4">
            <div>
              <label className="form-label">Spoteba tepl vody:</label>
              <div className="flex">
                <input
                  type="number"
                  className="form-input rounded-r-none"
                  placeholder="Spoteba"
                  {...register('hotWaterConsumption')}
                />
                <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                  l/den
                </span>
              </div>
            </div>
            
            <div>
              <label className="form-label">Spoteba pry:</label>
              <div className="flex">
                <input
                  type="number"
                  className="form-input rounded-r-none"
                  placeholder="Spoteba"
                  {...register('steamConsumption')}
                />
                <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                  kg/hod
                </span>
              </div>
            </div>
            
            <div>
              <label className="form-label">Jin spoteby:</label>
              <textarea
                className="form-input mt-2"
                rows={3}
                placeholder="Popite dal spoteby mimo elektinu a plyn (chlazen, stlaen vzduch, atd.)"
                {...register('otherConsumption')}
              />
            </div>
          </div>
        </div>

        {/* Kogeneran jednotka */}
        <div className="bg-red-50 p-4 rounded-lg border border-red-200">
          <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
            <Factory className="h-5 w-5 mr-2" />
            Kogeneran jednotka
          </h3>
          
          <div>
            <label className="form-label">Mte kogeneran jednotku (vlastn vrobn zdroj elektiny/tepla/chladu)?</label>
            <div className="flex gap-6 mt-3">
              <label className="flex items-center">
                <input 
                  type="radio" 
                  className="form-radio mr-2"
                  value="yes"
                  {...register('hasCogeneration')}
                />
                Ano
              </label>
              <label className="flex items-center">
                <input 
                  type="radio" 
                  className="form-radio mr-2"
                  value="no"
                  {...register('hasCogeneration')}
                />
                Ne
              </label>
            </div>
            
            {useWatch({ control, name: 'hasCogeneration' }) === 'yes' && (
              <div className="mt-4 space-y-4">
                <div>
                  <label className="form-label">Detaily kogeneran jednotky:</label>
                  <textarea
                    className="form-input mt-2"
                    rows={3}
                    placeholder="Popite typ, vkon, palivo, provozn reim kogeneran jednotky..."
                    {...register('cogenerationDetails')}
                  />
                </div>
                
                <FileUploadField
                  name="cogenerationPhotos"
                  label="Nahrt foto parametr kogeneran jednotky:"
                  accept=".jpg,.jpeg,.png,.pdf"
                  multiple={true}
                  formId={formId}
                  register={register}
                  setValue={setValue}
                  watch={watch}
                  helpText="Nahrvejte fotky ttk, technickch parametr nebo dokumentace"
                />
              </div>
            )}
          </div>
        </div>

        {/* Doplujc informace */}
        <div className="bg-gray-50 p-4 rounded-lg">
          <label className="form-label">Doplujc informace k energetice:</label>
          <textarea
            className="form-input mt-2"
            rows={3}
            placeholder="Dal dleit informace o energetickch potebch, plnech, specifickch poadavcch..."
            {...register('energyNotes')}
          />
        </div>
      </div>

      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <p className="text-blue-800 text-sm">
          <strong>Informace:</strong> Tyto daje nm pomohou lpe pochopit vae energetick poteby a navrhnout optimln een 
          zahrnujc vechny vae spoteby a zdroje energie.
        </p>
      </div>
    </div>
  )
}

export default FormStep8
