import { useFormContext, useWatch } from 'react-hook-form'
import { CheckCircle, Target, TrendingUp, Shield, Zap, DollarSign, Sun, BarChart3, Gift } from 'lucide-react'

const FormStep4 = () => {
  const { register, control } = useFormContext()

  // Watch for "other" purpose selection
  const otherPurpose = useWatch({
    control,
    name: 'goals.other'
  })

  // Watch for priorities to filter out selected options
  const priority1 = useWatch({ control, name: 'priority1' })
  const priority2 = useWatch({ control, name: 'priority2' })
  const priority3 = useWatch({ control, name: 'priority3' })

  // Priority options
  const priorityOptions = [
    { value: 'fve-overflow', label: 'spora z petok z FVE' },
    { value: 'peak-shaving', label: 'Posun spoteby (peak shaving)' },
    { value: 'backup-power', label: 'Zloha pi vpadku st' },
    { value: 'machine-support', label: 'Podpora vkonu stroj' },
    { value: 'power-reduction', label: 'Snen rezervovanho pkonu' },
    { value: 'energy-trading', label: 'Monost obchodovn s energi' },
    { value: 'subsidy', label: 'Zskn dotace' },
    { value: 'other', label: 'Jin el' }
  ]

  // Function to get available options for each priority select
  const getAvailableOptions = (currentPriority, excludePriorities) => {
    const selectedValues = excludePriorities.filter(Boolean)
    return priorityOptions.filter(option => 
      !selectedValues.includes(option.value) || option.value === currentPriority
    )
  }

  const goals = [
    { 
      id: 'fveOverflow', 
      label: 'spora z petok z FVE', 
      icon: Sun,
      description: 'Maximalizace vyuit energie z fotovoltaick elektrrny'
    },
    { 
      id: 'peakShaving', 
      label: 'Posun spoteby (peak shaving)', 
      icon: BarChart3,
      description: 'Snen nklad na elektinu optimalizac spoteby'
    },
    { 
      id: 'backupPower', 
      label: 'Zloha pi vpadku st', 
      icon: Shield,
      description: 'Zajitn dodvek pi vpadku distribun st'
    },
    { 
      id: 'machineSupport', 
      label: 'Podpora vkonu stroj', 
      icon: Zap,
      description: 'Podpora pi startu a provozu nronch stroj'
    },
    { 
      id: 'powerReduction', 
      label: 'Snen rezervovanho pkonu', 
      icon: TrendingUp,
      description: 'Optimalizace rezervovanho pkonu a snen fixnch nklad'
    },
    { 
      id: 'energyTrading', 
      label: 'Monost obchodovn s energi', 
      icon: DollarSign,
      description: 'Aktivn obchodovn s elektinou na trhu'
    },
    { 
      id: 'subsidy', 
      label: 'Zskn dotace', 
      icon: Gift,
      description: 'Vyuit dostupnch dotanch program'
    },
    { 
      id: 'other', 
      label: 'Jin el', 
      icon: Target,
      description: 'Specifick poadavky na vyuit baterie'
    }
  ]

  return (
    <div className="space-y-6">
      <div className="text-center mb-8">
        <div className="flex items-center justify-center mb-4">
          <Target className="h-8 w-8 text-primary-600 mr-3" />
          <h2 className="text-2xl font-bold text-gray-900">
            Cle a oekvn
          </h2>
        </div>
        <p className="text-gray-600">
          Vyberte vechny cle, kterch chcete doshnout pomoc bateriovho loit
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {goals.map((goal) => {
          const IconComponent = goal.icon
          return (
            <div key={goal.id} className="border border-gray-200 rounded-lg p-4 hover:border-primary-300 hover:shadow-md transition-all duration-200">
              <label className="flex items-start cursor-pointer">
                <input 
                  type="checkbox" 
                  className="form-checkbox mt-1 mr-3 flex-shrink-0"
                  {...register(`goals.${goal.id}`)}
                />
                <div className="flex-1">
                  <div className="flex items-center mb-2">
                    <IconComponent className="h-5 w-5 text-primary-600 mr-2" />
                    <span className="font-semibold text-gray-900">
                      {goal.label}
                    </span>
                  </div>
                  <p className="text-sm text-gray-600">
                    {goal.description}
                  </p>
                </div>
              </label>
            </div>
          )
        })}
      </div>

      {/* Conditional textarea for "other" purpose */}
      {otherPurpose && (
        <div className="bg-gray-50 p-4 rounded-lg border border-gray-200">
          <label className="form-label">
            Popite jin el vyuit bateriovho loit:
          </label>
          <textarea
            className="form-input mt-2"
            rows={3}
            placeholder="Popite detailnji v specifick el..."
            {...register('otherPurposeDescription')}
          />
        </div>
      )}

      {/* Doplujc informace */}
      <div className="mt-8">
        <label className="form-label">
          Doplujc informace k vaim clm
        </label>
        <textarea
          className="form-input"
          rows={4}
          placeholder="Popite detailnji vae oekvn a specifick poadavky..."
          {...register('goalDetails')}
        />
      </div>

      {/* Priorita cl */}
      <div className="bg-blue-50 p-6 rounded-lg">
        <h3 className="font-semibold text-blue-900 mb-4">Priorita vaich cl</h3>
        <div className="space-y-3">
          <div>
            <label className="form-label">1. Nejvy priorita</label>
            <select className="form-input" {...register('priority1')}>
              <option value="">Vyberte primrn cl</option>
              {getAvailableOptions(priority1, [priority2, priority3]).map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>
          
          <div>
            <label className="form-label">2. Druh priorita</label>
            <select className="form-input" {...register('priority2')}>
              <option value="">Vyberte sekundrn cl</option>
              {getAvailableOptions(priority2, [priority1, priority3]).map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>

          <div>
            <label className="form-label">3. Tet priorita (voliteln)</label>
            <select className="form-input" {...register('priority3')}>
              <option value="">Vyberte tercirn cl</option>
              {getAvailableOptions(priority3, [priority1, priority2]).map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </select>
          </div>
        </div>
      </div>

      <div className="bg-green-50 border border-green-200 rounded-lg p-4">
        <p className="text-green-800 text-sm">
          <strong>Tip:</strong> Jasn definovan cle a jejich priorita nm pomohou navrhnout bateriov loit, kter bude maximln efektivn pro vae poteby a pinese nejvy nvratnost investice.
        </p>
      </div>
    </div>
  )
}

export default FormStep4
