import { useFormContext, useWatch } from 'react-hook-form'
import { TrendingUp, Activity, FileText, Zap, Target, Shield, Battery, HelpCircle, Clock } from 'lucide-react'
import TimeSlider from '../TimeSlider'
import FileUploadField from '../FileUploadField'

const FormStep3 = ({ formId }) => {
  const { register, control, setValue, watch } = useFormContext()
  
  // Watch for conditional fields
  const hasDistributionCurves = useWatch({
    control,
    name: 'hasDistributionCurves'
  })

  const measurementType = useWatch({
    control,
    name: 'measurementType'
  })

  const hasCriticalConsumption = useWatch({
    control,
    name: 'hasCriticalConsumption'
  })

  const energyAccumulation = useWatch({
    control,
    name: 'energyAccumulation'
  })

  const requiresBackup = useWatch({
    control,
    name: 'requiresBackup'
  })

  // Watch for supplementary questions
  const hasElectricityProblems = useWatch({
    control,
    name: 'hasElectricityProblems'
  })

  const hasEnergyAudit = useWatch({
    control,
    name: 'hasEnergyAudit'
  })

  const hasOwnEnergySource = useWatch({
    control,
    name: 'hasOwnEnergySource'
  })

  const canProvideLoadSchema = useWatch({
    control,
    name: 'canProvideLoadSchema'
  })

  const backupDuration = useWatch({
    control,
    name: 'backupDuration'
  })

return (
    <div className="space-y-6">
        <div className="text-center mb-8">
            <div className="flex items-center justify-center mb-4">
                <TrendingUp className="h-8 w-8 text-primary-600 mr-3" />
                <h2 className="text-2xl font-bold text-gray-900">
                    Energetick poteby
                </h2>
            </div>
            <p className="text-gray-600">
                Detailn informace o energetick spoteb a poadavcch
            </p>
        </div>

        <div className="space-y-6">
            {/* M zkaznk odbrov kivky z distribun soustavy */}
                    <div className="bg-blue-50 p-4 rounded-lg border border-blue-200">
                        <label className="form-label">
                            <FileText className="inline h-5 w-5 mr-2" />
                            M zkaznk odbrov diagramy z distribunho portlu (nap. EZ, PRE, E.GD)?
                        </label>
                        <div className="flex gap-6 mt-3">
                            <label className="flex items-center">
                                <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="yes"
                            {...register('hasDistributionCurves')}
                                />
                                Ano
                            </label>
                            <label className="flex items-center">
                                <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="no"
                            {...register('hasDistributionCurves')}
                                />
                                Ne
                            </label>
                        </div>
                        
                        {hasDistributionCurves === 'yes' && (
                            <div className="mt-4 p-3 bg-white rounded border">
                                <p className="text-sm text-blue-800 font-medium mb-2">
                            Vydme data za minimln 3 msce
                                </p>
                                <FileUploadField
                                  name="distributionCurvesFile"
                                  label="Nahrt soubor s odbrovmi kivkami:"
                                  accept=".csv,.xlsx,.xls"
                                  multiple={false}
                                  formId={formId}
                                  register={register}
                                  setValue={setValue}
                                  watch={watch}
                                />
                            </div>
                        )}
                    </div>

                    {/* Distribun zem */}
                    <div className="bg-gray-50 p-4 rounded-lg">
                        <label className="form-label">
                            <FileText className="inline h-5 w-5 mr-2" />
                            Zkaznk se nachz v distribunm zem:
                        </label>
                        <div className="flex flex-wrap gap-6 mt-3">
                            <label className="flex items-center">
                                <input 
                            type="radio" 
                            className="form-radio mr-3"
                            value="cez"
                            {...register('distributionTerritory')}
                                />
                                <span>EZ</span>
                            </label>
                            <label className="flex items-center">
                                <input 
                            type="radio" 
                            className="form-radio mr-3"
                            value="pre"
                            {...register('distributionTerritory')}
                                />
                                <span>PRE</span>
                            </label>
                            <label className="flex items-center">
                                <input 
                            type="radio" 
                            className="form-radio mr-3"
                            value="egd"
                            {...register('distributionTerritory')}
                                />
                                <span>E.GD</span>
                            </label>
                            <label className="flex items-center">
                                <input 
                            type="radio" 
                            className="form-radio mr-3"
                            value="lds"
                            {...register('distributionTerritory')}
                                />
                                <span>LDS</span>
                            </label>
                        </div>
                        
                        {useWatch({ control, name: 'distributionTerritory' }) === 'lds' && (
                            <div className="mt-3 space-y-3">
                                <input
                                    type="text"
                                    className="form-input"
                                    placeholder="Nzev lokln distribun soustavy"
                                    {...register('ldsName')}
                                />
                                <input
                                    type="text"
                                    className="form-input"
                                    placeholder="Majitel LDS (nzev spolenosti)"
                                    {...register('ldsOwner')}
                                />
                                <textarea
                                    className="form-input"
                                    rows="2"
                                    placeholder="Dodaten informace o LDS"
                                    {...register('ldsNotes')}
                                />
                            </div>
                        )}
                    </div>

                    {/* Typ men spoteby */}
            <div className="bg-gray-50 p-4 rounded-lg">
                <label className="form-label">
                    <Activity className="inline h-5 w-5 mr-2" />
                    Typ men spoteby
                </label>
                <div className="flex gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-3"
                            value="quarter-hour"
                            {...register('measurementType')}
                        />
                        <span>tvrthodinov men (A-men)</span>
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-3"
                            value="other"
                            {...register('measurementType')}
                        />
                        <span>Jin</span>
                    </label>
                </div>
                
                {measurementType === 'other' && (
                    <div className="mt-3">
                        <input
                            type="text"
                            className="form-input"
                            placeholder="Popite typ men"
                            {...register('measurementTypeOther')}
                        />
                    </div>
                )}
            </div>

            {/* Slider pro spotebn vzorce */}
            <div className="bg-slate-50 p-6 rounded-lg border border-slate-200">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                    <Clock className="h-5 w-5 mr-2" />
                    Denn spotebn vzorce (0-24h)
                </h3>
                
                {/* Pracovn dny (Po-P) */}
                <div className="mb-6">
                    <h4 className="text-md font-medium text-gray-800 mb-3">Pracovn dny (Pondl - Ptek)</h4>
                    <TimeSlider 
                        register={register}
                        watch={useWatch}
                        control={control}
                        prefix="weekday"
                        label="Pracovn dny"
                    />
                </div>



                {/* Vkendy a svtky */}
                <div>
                    <h4 className="text-md font-medium text-gray-800 mb-3">Vkendy a svtky</h4>
                    <TimeSlider 
                        register={register}
                        watch={useWatch}
                        control={control}
                        prefix="weekend"
                        label="Vkendy"
                    />
                </div>
            </div>



            {/* Energetick parametry */}
            <div className="bg-green-50 p-6 rounded-lg border border-green-200">
                <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center">
                    <Zap className="h-5 w-5 mr-2" />
                    Energetick parametry
                </h3>
                
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {/* Ron spoteba elektiny */}
                    <div>
                        <label className="form-label">Ron spoteba elektiny:</label>
                        <div className="flex">
                            <input
                                type="number"
                                className="form-input rounded-r-none"
                                placeholder="Hodnota"
                                {...register('yearlyConsumption')}
                            />
                            <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                                MWh
                            </span>
                        </div>
                    </div>

                    {/* Prmrn denn spoteba */}
                    <div>
                        <label className="form-label">Prmrn denn spoteba:</label>
                        <div className="flex">
                            <input
                                type="number"
                                className="form-input rounded-r-none"
                                placeholder="Hodnota"
                                {...register('dailyAverageConsumption')}
                            />
                            <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                                kWh
                            </span>
                        </div>
                    </div>

                    {/* Maximln odbr (pika) */}
                    <div>
                        <label className="form-label">Maximln odbr v kW (pika):</label>
                        <div className="flex">
                            <input
                                type="number"
                                className="form-input rounded-r-none"
                                placeholder="Hodnota"
                                {...register('maxConsumption')}
                            />
                            <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                                kW
                            </span>
                        </div>
                    </div>

                    {/* Minimln odbr */}
                    <div>
                        <label className="form-label">Minimln odbr (v noci / o vkendu):</label>
                        <div className="flex">
                            <input
                                type="number"
                                className="form-input rounded-r-none"
                                placeholder="Hodnota"
                                {...register('minConsumption')}
                            />
                            <span className="bg-gray-100 border border-l-0 border-gray-300 px-3 py-3 rounded-r-lg text-gray-600 text-sm">
                                kW
                            </span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Kritick spoteby nebo stroje se pikami */}
            <div className="bg-yellow-50 p-4 rounded-lg border border-yellow-200">
                <label className="form-label">
                    <Target className="inline h-5 w-5 mr-2" />
                    Kritick spoteby nebo stroje se pikami?
                </label>
                <div className="flex gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="yes"
                            {...register('hasCriticalConsumption')}
                        />
                        Ano
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="no"
                            {...register('hasCriticalConsumption')}
                        />
                        Ne
                    </label>
                </div>
                
                {hasCriticalConsumption === 'yes' && (
                    <div className="mt-3">
                        <textarea
                            className="form-input"
                            rows={3}
                            placeholder="Popite kritick spoteby nebo stroje se pikami..."
                            {...register('criticalConsumptionDescription')}
                        />
                    </div>
                )}
            </div>

            {/* Kolik energie denn by chtl zkaznk akumulovat */}
            <div className="bg-purple-50 p-4 rounded-lg border border-purple-200">
                <label className="form-label">
                    <Battery className="inline h-5 w-5 mr-2" />
                    Kolik energie denn by chtl zkaznk akumulovat?
                </label>
                <div className="flex gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="unknown"
                            {...register('energyAccumulation')}
                        />
                        Nev
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="specific"
                            {...register('energyAccumulation')}
                        />
                        Konkrtn hodnota
                    </label>
                </div>
                
                {energyAccumulation === 'specific' && (
                    <div className="mt-3 flex items-center gap-2">
                        <input
                            type="number"
                            className="form-input w-32"
                            placeholder="Hodnota"
                            {...register('energyAccumulationAmount')}
                        />
                        <span className="text-gray-600">kWh</span>
                    </div>
                )}
            </div>

            {/* Poadovan poet cykl baterie denn */}
            <div className="bg-indigo-50 p-4 rounded-lg border border-indigo-200">
                <label className="form-label">Poadovan poet cykl baterie denn:</label>
                <div className="flex flex-wrap gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="once"
                            {...register('batteryCycles')}
                        />
                        1x denn
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="multiple"
                            {...register('batteryCycles')}
                        />
                        Vcekrt denn
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="recommend"
                            {...register('batteryCycles')}
                        />
                        Neznmo - doporuit
                    </label>
                </div>
            </div>

            {/* Je poadavek na zlohovn objektu pi vpadku */}
            <div className="bg-red-50 p-4 rounded-lg border border-red-200">
                <label className="form-label">
                    <Shield className="inline h-5 w-5 mr-2" />
                    Je poadavek na zlohovn objektu pi vpadku?
                </label>
                <div className="flex gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="yes"
                            {...register('requiresBackup')}
                        />
                        Ano
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="no"
                            {...register('requiresBackup')}
                        />
                        Ne
                    </label>
                </div>
                
                {requiresBackup === 'yes' && (
                    <div className="mt-3">
                        <label className="form-label text-sm">Pro kter sti je zlohovn douc:</label>
                        <textarea
                            className="form-input"
                            rows={2}
                            placeholder="Popite kter sti objektu chcete zlohovat..."
                            {...register('backupDescription')}
                        />
                    </div>
                )}
            </div>

            {/* Jak dlouhou vdr zlohy zkaznk poaduje */}
            <div className="bg-orange-50 p-4 rounded-lg border border-orange-200">
                <label className="form-label">Jak dlouhou vdr zlohy zkaznk poaduje?</label>
                <div className="flex flex-wrap gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="minutes"
                            {...register('backupDuration')}
                        />
                        Destky minut
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="hours-1-3"
                            {...register('backupDuration')}
                        />
                        1-3 hodiny
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="hours-3-plus"
                            {...register('backupDuration')}
                        />
                        Vce ne 3 hodiny
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="exact-time"
                            {...register('backupDuration')}
                        />
                        Pesn as
                    </label>
                </div>
                
                {backupDuration === 'exact-time' && (
                    <div className="mt-3 flex items-center gap-2">
                        <label className="form-label text-sm mr-2">Poet hodin:</label>
                        <input
                            type="number"
                            className="form-input w-32"
                            placeholder="Nap. 8"
                            min="0.5"
                            step="0.5"
                            {...register('backupDurationHours')}
                        />
                        <span className="text-gray-600">hodin</span>
                    </div>
                )}
            </div>

            {/* M bt baterie zena podle ceny elektiny */}
            <div className="bg-teal-50 p-4 rounded-lg border border-teal-200">
                <label className="form-label">
                    M bt baterie zena podle ceny elektiny (nap. spotov trh, optimalizace nkup)?
                </label>
                <div className="flex gap-6 mt-3">
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="yes"
                            {...register('priceOptimization')}
                        />
                        Ano
                    </label>
                    <label className="flex items-center">
                        <input 
                            type="radio" 
                            className="form-radio mr-2"
                            value="no"
                            {...register('priceOptimization')}
                        />
                        Ne
                    </label>
                </div>
            </div>

            {/* Doplujc technick otzky */}
            <div className="bg-amber-50 p-6 rounded-lg border border-amber-200">
                <h3 className="font-semibold text-amber-900 mb-4 flex items-center">
                    <HelpCircle className="h-5 w-5 mr-2" />
                    Doplujc technick otzky
                </h3>
                
                <div className="space-y-6">
                    <div className="bg-white p-4 rounded-lg border border-amber-100">
                        <label className="form-label">Mte problmy s vpadky elektiny?</label>
                        <div className="flex gap-6 mt-2">
                            <label className="flex items-center">
                                <input 
                                    type="radio" 
                                    className="form-radio mr-2"
                                    value="yes"
                                    {...register('hasElectricityProblems')}
                                />
                                Ano
                            </label>
                            <label className="flex items-center">
                                <input 
                                    type="radio" 
                                    className="form-radio mr-2"
                                    value="no"
                                    {...register('hasElectricityProblems')}
                                />
                                Ne
                            </label>
                        </div>
                        {hasElectricityProblems === 'yes' && (
                            <input
                                type="text"
                                className="form-input mt-3"
                                placeholder="Uvete jak vpadky za rok"
                                {...register('electricityProblemsDetails')}
                            />
                        )}
                    </div>

                    <div className="bg-white p-4 rounded-lg border border-amber-100">
                        <label className="form-label">Mte zpracovan energetick audit?</label>
                        <div className="flex gap-6 mt-2">
                            <label className="flex items-center">
                                <input 
                                    type="radio" 
                                    className="form-radio mr-2"
                                    value="yes"
                                    {...register('hasEnergyAudit')}
                                />
                                Ano
                            </label>
                            <label className="flex items-center">
                                <input 
                                    type="radio" 
                                    className="form-radio mr-2"
                                    value="no"
                                    {...register('hasEnergyAudit')}
                                />
                                Ne
                            </label>
                        </div>
                        {hasEnergyAudit === 'yes' && (
                            <input
                                type="text"
                                className="form-input mt-3"
                                placeholder="Prosme zaslat emailem"
                                {...register('energyAuditDetails')}
                            />
                        )}
                    </div>

                    <div className="bg-white p-4 rounded-lg border border-amber-100">
                        <label className="form-label">Mte vlastn vrobn zdroj elektiny/tepla?</label>
                        <div className="flex gap-6 mt-2">
                            <label className="flex items-center">
                                <input 
                                    type="radio" 
                                    className="form-radio mr-2"
                                    value="yes"
                                    {...register('hasOwnEnergySource')}
                                />
                                Ano
                            </label>
                            <label className="flex items-center">
                                <input 
                                    type="radio" 
                                    className="form-radio mr-2"
                                    value="no"
                                    {...register('hasOwnEnergySource')}
                                />
                                Ne
                            </label>
                        </div>
                        {hasOwnEnergySource === 'yes' && (
                            <input
                                type="text"
                                className="form-input mt-3"
                                placeholder="Popite jak zdroj mte a jak ho vyuvte"
                                {...register('ownEnergySourceDetails')}
                            />
                        )}
                    </div>

                </div>
            </div>
        </div>

        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
            <p className="text-blue-800 text-sm">
                <strong>Informace:</strong> Tyto daje jsou klov pro nvrh optimlnho bateriovho loit pesn podle vaich poteb.
            </p>
        </div>
    </div>
)
}

export default FormStep3
