import { useFormContext, useWatch } from 'react-hook-form'
import { MessageSquare, FileText, Calendar } from 'lucide-react'

const FormStep7 = () => {
  const { register, control, watch } = useFormContext()

  // Watch for "other" procedure selection
  const otherProcedure = useWatch({
    control,
    name: 'proposedSteps.other'
  })

  return (
    <div className="space-y-6">
      <div className="text-center mb-8">
        <h2 className="text-2xl font-bold text-gray-900">
          7. Navren postup a poznmky
        </h2>
        <p className="text-gray-600">
          Specifick poadavky a zjem zkaznka
        </p>
      </div>

      <div className="space-y-6">
        {/* Navren postup */}
        <div>
          <label className="form-label">
            <Calendar className="inline h-5 w-5 mr-2" />
            Navren postup
          </label>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-3">
            <label className="flex items-center p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
              <input 
                type="checkbox" 
                className="form-checkbox mr-3"
                {...register('proposedSteps.preliminary')}
              />
              <span>Pedbn nabdka</span>
            </label>
            <label className="flex items-center p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
              <input 
                type="checkbox" 
                className="form-checkbox mr-3"
                {...register('proposedSteps.technical')}
              />
              <span>Technick prohldka</span>
            </label>
            <label className="flex items-center p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
              <input 
                type="checkbox" 
                className="form-checkbox mr-3"
                {...register('proposedSteps.detailed')}
              />
              <span>Pprava zakzky a pipojen</span>
            </label>
            <label className="flex items-center p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
              <input 
                type="checkbox" 
                className="form-checkbox mr-3"
                {...register('proposedSteps.consultancy')}
              />
              <span>Konzultace s energetikem</span>
            </label>
            <label className="flex items-center p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
              <input 
                type="checkbox" 
                className="form-checkbox mr-3"
                {...register('proposedSteps.support')}
              />
              <span>Monost obchodovn s energi</span>
            </label>
            <label className="flex items-center p-3 border border-gray-200 rounded-lg hover:bg-gray-50 cursor-pointer">
              <input 
                type="checkbox" 
                className="form-checkbox mr-3"
                {...register('proposedSteps.other')}
              />
              <span>Jin postup</span>
            </label>
          </div>

          {/* Conditional textarea for "Other" procedure */}
          {otherProcedure && (
            <div className="mt-4 p-4 bg-yellow-50 border border-yellow-200 rounded-lg">
              <label className="form-label text-sm">
                Popite jin postup:
                <span className="text-red-500 ml-1">*</span>
              </label>
              <textarea
                className="form-input mt-2"
                rows={4}
                placeholder="Popite podrobn, jak alternativn postup si pedstavujete..."
                {...register('proposedSteps.otherDescription', {
                  required: otherProcedure ? 'Popis jinho postupu je povinn' : false
                })}
              />
            </div>
          )}
        </div>

        {/* Poznmky */}
        <div>
          <label className="form-label">
            <FileText className="inline h-5 w-5 mr-2" />
            Poznmky
          </label>
          <p className="text-sm text-gray-600 mb-3">
            Doplte ve, co povaujete za dleit doplnit!
          </p>
          <textarea
            className="form-input"
            rows={6}
            placeholder="Zde mete uvst jakkoliv dodaten informace, specifick poadavky, dotazy nebo pipomnky, kter povaujete za dleit pro ppravu nabdky bateriovho loit..."
            {...register('additionalNotes')}
          />
        </div>

        {/* Souhlas s dalm postupem */}
        <div className="bg-green-50 border border-green-200 rounded-lg p-6">
          <h3 className="font-semibold text-green-900 mb-4">Souhlas s dalm postupem</h3>
          <div className="space-y-3">
            <label className="flex items-start">
              <input 
                type="checkbox" 
                className="form-checkbox mt-1 mr-3 flex-shrink-0"
                {...register('agreements.dataProcessing')}
              />
              <span className="text-sm">
                Souhlasm se zpracovnm osobnch daj v rozsahu uvedenm v tomto formuli za elem 
                ppravy nabdky bateriovho loit, FVE nebo jinho energetickho een a nsledn komunikace.
              </span>
            </label>
            <label className="flex items-start">
              <input 
                type="checkbox" 
                className="form-checkbox mt-1 mr-3 flex-shrink-0"
                {...register('agreements.technicalVisit')}
              />
              <span className="text-sm">
                Souhlasm s nvtvou technika spolenosti Electree za elem pesnjho 
                zhodnocen monost instalace bateriovho loit.
              </span>
            </label>
            <label className="flex items-start">
              <input 
                type="checkbox" 
                className="form-checkbox mt-1 mr-3 flex-shrink-0"
                {...register('agreements.marketing')}
              />
              <span className="text-sm">
                Souhlasm s zaslnm obchodnch sdlen a informac o novch produktech 
                a slubch spolenosti Electree (tento souhlas mete kdykoliv odvolat).
              </span>
            </label>
          </div>
        </div>
      </div>

      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <p className="text-blue-800 text-sm">
          <strong>Dkujeme</strong> za vyplnn kompletnho dotaznku! Vae daje nm pomohou pipravit 
          co nejpesnj a nejvhodnj nabdku bateriovho loit pro vae poteby. N specialista se vm ozve do 2 pracovnch dn.
        </p>
      </div>
    </div>
  )
}

export default FormStep7
